import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'My Todo List';
  todos = [
    { 
    label: 'Create Portfolio', 
    done: false, 
    priority: 3 
    },
    { 
      label: 'Clean Car', 
      done: true, 
      priority: 1 
    },
    { 
      label: 'Clean House', 
      done: false, 
      priority: 4 
    },
    { 
      label: 'Wash Clothes', 
      done: false, 
      priority: 5 
    },    
    ];

    addTodo(newTodoLabel) {
      var newTodo = {
        label: newTodoLabel,
        priority: 1,
        done: false
      };
      this.todos.push(newTodo);
    }
    deleteTodo(todo) {
      this.todos = this.todos.filter( t => t.label !== todo.label );
    }
  }  


